import React, { useState, useEffect } from "react";
import "../assets/css/KnowledgeBase.css"; // CSS

function KnowledgeBase() {
  // 
  const [questions, setQuestions] = useState([]); // 
  const [filteredQuestions, setFilteredQuestions] = useState([]); // 
  const [searchQuery, setSearchQuery] = useState(""); // 
  const [sortBy, setSortBy] = useState("likes"); // 
  const [categoryFilter, setCategoryFilter] = useState("all"); // 
  const [selectedQuestion, setSelectedQuestion] = useState(null); // 
  const [showModal, setShowModal] = useState(false); //  modal 

  //  fetch
  useEffect(() => {
    const fetchedQuestions = [
      {
        id: 1,
        text: "What is AI?",
        likes: 10,
        created_at: "2024-02-21",
        category: "",
        answer: "AI is the simulation of human intelligence in machines.",
      },
      {
        id: 2,
        text: "How does GPT work?",
        likes: 20,
        created_at: "2024-02-22",
        category: "AI",
        answer: "GPT uses transformers to predict the next word in a sequence.",
      },
      {
        id: 3,
        text: "What is React?",
        likes: 5,
        created_at: "2024-02-23",
        category: "",
        answer: "React is a JavaScript library for building user interfaces.",
      },
      {
        id: 4,
        text: "How to manage a project?",
        likes: 12,
        created_at: "2024-03-01",
        category: "",
        answer:
          "Use tools like Gantt charts, agile boards, and regular reviews.",
      },
    ];

    setQuestions(fetchedQuestions);
    updateFilteredQuestions(
      fetchedQuestions,
      searchQuery,
      sortBy,
      categoryFilter
    );
  }, [searchQuery, sortBy, categoryFilter]);

  // 
  const updateFilteredQuestions = (questions, query, sort, category) => {
    let filtered = questions.filter((q) =>
      q.text.toLowerCase().includes(query.toLowerCase())
    );

    if (category !== "all") {
      filtered = filtered.filter((q) => q.category === category);
    }

    if (sort === "likes") {
      filtered.sort((a, b) => b.likes - a.likes);
    } else if (sort === "newest") {
      filtered.sort((a, b) => new Date(b.created_at) - new Date(a.created_at));
    }

    setFilteredQuestions(filtered);
  };

  // 
  const handleShowDetail = (question) => {
    setSelectedQuestion(question);
    setShowModal(true);
  };

  return (
    <div className="knowledge-base">
      <h1></h1>

      {/*  */}
      <div className="knowledge-controls">
        <input
          type="text"
          placeholder="...."
          value={searchQuery}
          onChange={(e) => setSearchQuery(e.target.value)}
          className="search-box"
        />

        <select
          className="category-dropdown"
          value={categoryFilter}
          onChange={(e) => setCategoryFilter(e.target.value)}
        >
          <option value="all"></option>
          <option value=""></option>
          <option value="AI">AI</option>
          <option value=""></option>
          <option value=""></option>
        </select>

        <select
          className="sort-dropdown"
          value={sortBy}
          onChange={(e) => setSortBy(e.target.value)}
        >
          <option value="likes"></option>
          <option value="newest"></option>
        </select>
      </div>

      {/*  */}
      <table className="knowledge-table">
        <thead>
          <tr>
            <th>#</th>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
          </tr>
        </thead>
        <tbody>
          {filteredQuestions.map((q, index) => (
            <tr key={q.id}>
              <td>{index + 1}</td>
              <td>{q.text}</td>
              <td>{q.category}</td>
              <td> {q.likes}</td>
              <td>{q.created_at}</td>
              <td>
                <button
                  className="detail-btn"
                  onClick={() => handleShowDetail(q)}
                >
                   
                </button>
              </td>
            </tr>
          ))}
        </tbody>
      </table>

      {/* Modal  */}
      {showModal && selectedQuestion && (
        <div className="modal-overlay" onClick={() => setShowModal(false)}>
          <div className="modal-content" onClick={(e) => e.stopPropagation()}>
            <h2>{selectedQuestion.text}</h2>
            <p>{selectedQuestion.answer}</p>
            <button className="close-btn" onClick={() => setShowModal(false)}>
              
            </button>
          </div>
        </div>
      )}
    </div>
  );
}

export default KnowledgeBase;
