import React, { useState, useEffect } from "react";
import axios from "axios"; //  axios: npm install axios

function Project() {
  // 
  const [question, setQuestion] = useState(""); // 
  const [answer, setAnswer] = useState(""); // GPT 
  const [likes, setLikes] = useState(0); // 
  const [loading, setLoading] = useState(false); // 
  const [hasLiked, setHasLiked] = useState(false); // 

  // 
  useEffect(() => {
    const likedStatus = localStorage.getItem("liked_" + question);
    if (likedStatus === "true") {
      setHasLiked(true);
    }
  }, [question]);

  // 
  const handleAskQuestion = async () => {
    if (!question) return; // 

    setLoading(true); // 

    try {
      //  http://localhost:5000/ask
      const response = await axios.post("http://localhost:5000/ask", {
        question: question,
      });

      //  GPT 
      setAnswer(response.data.answer);
    } catch (error) {
      console.error(":", error);
      setAnswer("");
    }

    setLoading(false); // 
  };

  // 
  useEffect(() => {
    const likedStatus = localStorage.getItem("liked_" + question);
    setHasLiked(likedStatus === "true");
  }, [question]);

  const handleLike = () => {
    if (!hasLiked) {
      const newLikes = likes + 1;
      setLikes(newLikes);
      setHasLiked(true);
      localStorage.setItem("liked_" + question, "true"); // 
      localStorage.setItem("likes_" + question, newLikes); // 
    }
  };

  return (
    <div className="App">
      <h1></h1>

      <div className="question-box">
        <input
          type="text"
          placeholder=" GPT ..."
          value={question}
          onChange={(e) => setQuestion(e.target.value)} // 
        />
        <button onClick={handleAskQuestion} disabled={loading || !question}>
          {loading ? "..." : ""} {/*  */}
        </button>
      </div>

      {answer && (
        <div className="answer-box">
          <h2> AI </h2>
          <p>{answer}</p>
          <button onClick={handleLike} disabled={hasLiked}>
             {likes} 
          </button>
        </div>
      )}
    </div>
  );
}

export default Project;
